const axios = require("axios");
//toast_time 显示时间
const toast_time = 1000;

export default {
  data: function() {
    return {
      account: "",
      password: "",
      name: ""
    };
  },
  created: async function() {
    //页面初建时帮助用户自助登陆
    /**@augments
     * ?????如何在前端页面接受 dnxn的token，然后由前端转发给后端
     */
    this.$vux.loading.show({
      text: "自助登陆中..."
    });
    let login_res = await axios({
      url: this.$common.checkUserUrl,
      method: "get"
    });
    this.$vux.loading.hide();
    if (login_res.data.userLogin) {
      this.$vux.toast.show({
        text: "登陆成功",
        time: toast_time
      });
      setTimeout(() => {
        this.$router.push({ name: "searchPage" });
      }, toast_time);
    } else {
      this.$vux.toast.show({
        text: "登陆失败,请重新绑定信息",
        time: 2000,
        type: "warn"
      });
    }
  },
  methods: {
    confirm: async function() {
      console.log(this.account, this.password, this.name);
      //loading 框显示出来
      this.$vux.loading.show({
        text: "正在绑定",
        isShowMask: true
      });
      let result = await axios({
        method: "post",
        data: {
          stuId: this.account,
          pswd: this.password,
          name: this.name
        },
        url: this.$common.rebindUrl
      });
      if (result.data.success) {
        //绑定成功
        this.$vux.loading.hide();
        this.$vux.toast.show({
          text: "绑定成功",
          type: "success",
          isShowMask: true,
          time: toast_time
        });
        //绑定成功，主动跳转至searchPage主页面
        setTimeout(() => {
          this.$router.push({ name: "searchPage" });
        }, toast_time);
      } else {
        //绑定失败
        this.$vux.loading.hide();
        this.$vux.toast.show({
          text: result.data.err.msg,
          type: "cancel",
          isShowMask: true,
          time: toast_time
        });
      }
      console.log(result.data);
    }
  },
  components: {}
};



