export default {
  props: ["books", "number"],
  data: function() {
    return {};
  },
  methods: {
    gotoBookDetail: function(book) {
      this.$router.push({
        name: "bookDetail",
        params: {
          book: book
        }
      });
    }
  },
  created: function() {
    console.log(this.books);
  }
};



