export default {
  props: ["searchStrFromFather"],
  data: function() {
    return {
      searchStr: ""
    };
  },
  methods: {
    doSearch: function() {
      //console.log("发出了搜索事件！");
      this.$emit("search", this.searchStr);
    },
    //从首页展示跳转到搜索信息展示区
    toSearchInfoPage: function() {
      //console.log("发出了跳转事件！");
      this.$emit("gotoinfo");
    }
  },
  watch: {
    searchStr: function() {
      this.doSearch();
    }
  },
  created: function() {
    this.searchStr = this.searchStrFromFather;
  }
};



