export default {
  props: ["classes"],
  data: function() {
    return {};
  },
  methods: {
    isLast: function(order) {
      if (order === this.classes.length - 1) {
        return true;
      }
    },
    gotoClass: function(item) {
      this.$emit("goinClass", item);
    }
  }
};



